html {
  font: 62.5% Arial, Helvetica, sans-serif;
  color: white;
}

body {
  margin: 0;
  display: grid;
  min-height: 100vh;
  min-height: 100dvh;
  place-items: center;
  background: #121214;
}

:root {
  --clr-green: hsl(120, 100%, 75%);
  --clr-red: #ff8080;
}

.clr {
  &-green {
    color: var(--clr-green);
  }
  &-red {
    color: var(--clr-red);
  }
}

table {
  font-size: 2.4rem;
  border-collapse: collapse;
  width: 100%;
  background: #27272c;
  border-block: 0.5rem solid rgb(8, 70, 133);
}

td,
th {
  text-align: left;
}

.heading {
  margin-left: 3.4rem;
}

.row,
.heading {
  padding: 1rem;
  display: grid;
  gap: 1rem;
  grid-template-columns: 1fr 1fr 3rem;
}

.row {
  background: hsl(240, 6%, 20%);
  &:nth-of-type(odd) {
    background: hsl(240, 6%, 24%);
  }
}

.slot {
  display: grid;
  grid-template-columns: 2.4rem 1fr;
  gap: 1rem;
  align-items: center;
  justify-content: flex-start;
  &-icon {
    image-rendering: pixelated;
    justify-self: center;
  }
}

.value {
  display: flex;
  gap: 1rem;
  align-items: center;
  justify-content: flex-end;
  &::after {
    font-size: 1.5rem;
  }
  &[data-state='increasing']::after {
    content: '▲';
    color: var(--clr-green);
  }
  &[data-state='decreasing']::after {
    content: '▼';
    color: var(--clr-red);
  }
}

.bis {
  display: none;
}

.isBest .bis {
  padding: 0;
  height: 66%;
  place-self: center;
  display: grid;
  place-items: center;
  fill: orange;
}

@media screen and (min-width: 768px) {
  table {
    width: 76.8rem;
  }
}
